.about {
    width: 100%;
    height: 100%;
    position: absolute;
}

.container {
    position: relative;
    height: 100%;
    width: 100%;
}

.secondLayer {
    position: relative;
    width: 100%;
    height: 100%;
}

.about-container {
    width: 90%;
    height: 75%;
    margin: 10%;
    margin-left: 5%;
    margin-right: 5%;
    border-radius: 20px;
}

.box {
    height: 80%;
    width: 29%;
    border-radius: 20px;
    float: left;
    color: white;
    display: table;
    text-align: center;
    margin: 10px;
    border: 7px solid black;
}

.box:hover {
    color: #2f97d8;
    cursor: pointer;
}

.cell-header {
    display: table-cell;
    font-size: 30px;
    padding-top: 5%;
}

.cell-header-container {
    width: 100%;
    height: 15%;
    background-color: #1f3644;
    margin-top: 90%;
    display: table;
}

.sport-container {
    background-image: url('./handball.png');
    background-size: cover;
}

.other-container {
    background-image: url('./background.jpg');
    background-size: cover;
}

.it-skills-container {
    background-image: url('./WebsiteImage.PNG');
    background-size: 100%;
}

.transition-container {
    display: none;
}

.itSkills2-enter {
    display: block;
}

.itSkills2-enter-active{
    animation-name: 'show';
}

.itSkills2-exit {
    display: block;
    opacity: 1;
}

.itSkills2-exit-active {
    opacity: 0;
}

@keyframes show {
    from {height: 0%;}
    to {height: 100%;}
  }

@keyframes hide {
    from {height: 100%;}
    to {height: 0%;}
  }